<h2 id="button-an-niu"><a class="header-anchor" href="#button-an-niu" aria-hidden="true">¶</a> Button 按钮</h2>
<p>定义了平时常用的按钮主题类型。</p>
<h2 id="button-zhi-chi-5-chong-zhu-ti-lei-xing"><a class="header-anchor" href="#button-zhi-chi-5-chong-zhu-ti-lei-xing" aria-hidden="true">¶</a> Button 支持5种主题类型</h2>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;body&gt;&lt;m-button&gt;Default&lt;/m-button&gt;\n&lt;m-button type=\&quot;info\&quot;&gt;Info&lt;/m-button&gt;\n&lt;m-button type=\&quot;primary\&quot;&gt;Primary&lt;/m-button&gt;\n&lt;m-button type=\&quot;warning\&quot;&gt;Warning&lt;/m-button&gt;\n&lt;m-button type=\&quot;danger\&quot;&gt;Danger&lt;/m-button&gt;\n&lt;m-button type=\&quot;success\&quot;&gt;Success&lt;/m-button&gt;\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
