<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>Info<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>Primary<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span>&gt;</span>Warning<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
